/**
 * All of the CSS for your admin-specific functionality should be
 * included in this file.
 */

#me_pages {
    .nav-tab-wrapper {
        margin: 10px 0 25px;
    }

    .button {
        margin-top: 20px;
    }

    .version {
        position: relative;
        top: 15px;
        float: right;
    }

    .form-table {
        background-color: #ffffff;
        border: 1px solid #9d9d9d;
        margin-bottom: 50px;

        &:last-of-type {
            margin-bottom: 0;
        }

        th,
        td {
            padding: 20px 30px;
        }

        tr:first-child > th,
        tr:first-child > td {
            padding-top: 40px;
        }

        tr:last-child > th,
        tr:last-child > td {
            padding-bottom: 40px;
        }
    }

    p.description {
        font-size: 12px;
        line-height: 1.2em;
        color: #9d9d9d;
    }

    ul {
        list-style-type: disc;

        li {
            margin-left: 20px;
        }
    }

    .select2-selection,
    .select2-selection > span:focus {
        outline: none !important;
        outline-style: none;
        outline-width: 0;
    }

    input[type="text"],
    select {
        padding: 6px;
        height: auto;
        min-width: 369px;
    }

    select[multiple] {
        height: 200px;
    }

    .select2 li {
        margin-bottom: 0;
    }
}

#market-exporter table td > input[type="checkbox"] {
    margin: 0 0 0 8px;
}


